import searchInput from '@/components/searchInput/index.vue';
import selectInput from '@/components/selectInput/selectInput.vue';
export default {
	components: { searchInput, selectInput },
	data() {
		return {
			list: [
				{
					id: 1,
					userId:3,
					name: '迪丽热巴',
					images: require('@/static/image/girl.jpg'),
					updateTime: '下午 5:10',
					show: false,
					isTop: true
				},
				{
					id: 2,
					userId:4,
					name: '小贱贱',
					images: require('@/static/image/boy.jpg'),
					updateTime: '下午 5:10',
					show: false
				},
				{
					id: 3,
					userId:2,
					name: '陈冠希',
					images: require('@/static/image/guanxi.jpg'),
					updateTime: '下午 5:10',
					show: false
				}
			],
			selectShow: false,
			options: [
				{
					text: '删除',
					style: {
						backgroundColor: '#dd524d',
						fontSize: '24rpx'
					}
				}
			],
			selectList: [{ id: '1', name: '添加朋友', icon: 'man-add-fill' }, { id: '2', name: '扫一扫', icon: 'scan' }, { id: '3', name: '收付款', icon: 'fingerprint' }]
		};
	},
	methods: {
		//打开或者关闭弹窗
		showSelect(){
			this.selectShow = !this.selectShow;
		},
		//action 点击事件
		click(index, index1) {
			if (index1 == 0) {
				this.list.splice(index, 1);
			} 
		},
		//action 打开事件
		open(index) {
			this.list[index].show = true;
			this.list.map((val, idx) => {
				if (index != idx) this.list[idx].show = false;
			});
		},
		//点击导航栏自定义按钮
		onNavigationBarButtonTap({ index }) {
			if (index == 0) {
				this.showSelect()
			}
		},
		//跳转
		linkTo({ id, name, images ,userId }) {
			this.$u.route({
				url: 'pages/chat/chat',
				params: { messageId: id,fromUserId:userId}
			});
		},
		//关闭弹窗
		closeSelect(){
			//小程序兼容
			this.selectShow = false;
		},
		//扫码
		checkSelect(index) {
			if (index == 1) {
				uni.vibrateLong();
				//扫码
				uni.scanCode({
					success: function(res) {
						// console.log('条码内容：' + res.result);
						this.$u.toast('条码类型:', res.scanType);
					}
				});
			}
		},
	},
};
